
.page {
    height: 80%;
    width: 100%;
    display: flex;
    font-size: 0.5em;
    margin: 0;
    padding: 0;

}

.signup-cover {
    background-color: white;
    width: 40%;
    height: 60%;
    position: absolute;
    border-radius: 1em;
    box-shadow: 0 0.188em 1.550em rgb(156, 156, 156);
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-sizing: border-box;
    color: #3498db ;
    font-weight: bold;
}

.alt-login {
    width: 60%;
    height: 3em;
    position: center;
}

.alt-login div {
    width: 70%;
    height: 90%;
    border-radius: 0.25em;
    background-color: rgb(0, 60, 255);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
}

.google {background-image: url("../images/google.png");}
.facebook {background-image: url("../images/Facebook-Logo.png");}

input {
    border: 2px;
    background-color: rgb(229, 226, 226);
    width: 80%;
    border-radius: 0.25em;
    text-align:center;
    padding: 2%;
    font-size: 0.7em;
    font-family: 'Times New Roman', Times, serif;
}

input:focus {
    outline-color: rgb(32, 177, 255);
}

.buttons {
    width: 30%;
    height: 3em;
    background-color: rgb(0, 0, 0);
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
    transition: 0.5s;
    border-radius: 0.25em;
    cursor: pointer;
    margin-top: 10px;
}

.buttons:hover { 
    transform: scale(1.25);
}

.text {

    position: relative;
    bottom: 0.5%;
}

.hide {
    position: absolute;
    transform: scale(0);
}

.login-popup {
    position:absolute;
    height: 20em;
    width: 20em;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 0.15em;
    background-color: rgb(0, 0, 0);
    color: white;
    bottom: 0;
    transform: scale(1);
    transition: 1.5s;
    margin: center;
    text-align: center;
}

.blue {
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url('../images/google.png');
}
